/**
 * @description  - 
 * @param {string} elm - idclass  document.querySelect -eg: #id
 * @param {string} type = [single|array] -  
 * @param {Function} callback - 
 * @param {object} that -  vue2this ,vue3: getCurrentInstance();
 */
export const getElementRect = (that) => (elm, type = 'single', callback) => {


	uni
		.createSelectorQuery()
		.in(that)[type === 'array' ? 'selectAll' : 'select'](elm)
		.boundingClientRect()
		.exec(data => {
			callback(data[0]);
		});

















}